/* coming-soon-regular - latin */
@font-face {
  font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
  font-family: "Coming Soon";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/coming-soon-v19-latin-regular.eot"); /* IE9 Compat Modes */
  src: url("../fonts/coming-soon-v19-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/coming-soon-v19-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/coming-soon-v19-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/coming-soon-v19-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/coming-soon-v19-latin-regular.svg#ComingSoon") format("svg"); /* Legacy iOS */
}

